import { BrowserRouter, Routes, Route } from "react-router-dom";
import Home from "./routes/Home";
import Movie from "./routes/Detail";
function App() {
  return (
    <BrowserRouter>
      <Routes>
        <Route path={`${process.env.PUBLIC_URL}/`} element={<Home />} />
        <Route path="/movie/:id" element={<Movie />} />
      </Routes>
    </BrowserRouter>
  );
}
export default App;
